"use client";

import React, { useRef, useEffect } from "react";
import Image from "next/image";
import { motion, useScroll, useTransform } from "motion/react";
import Link from "next/link";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import ProcessHome from "@/components/sections/Processhome";

// Register GSAP plugins
if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger);
}

// Process stages data
const processStages = [
  {
    number: "01",
    title: "SKETCH DESIGN",
    image: "/img/p3.avif",
    description: "We kick off with a site visit and creative consultation to define your vision, style, and functional needs. We'll also uncover new opportunities and potential limitations. Based on this, we craft a series of hand-drawn design sketches to provide an initial visual concept that reflects your brief. Through collaboration and refinement, we shape the concept to align with your goals, timeline, and budget."
  },
  {
    number: "02",
    title: "DESIGN DEVELOPMENT",
    image: "/img/p5.jpg",
    description: "Building on the approved sketch design, we develop detailed drawings and 3D models. This stage involves refining spatial relationships, material selections, and technical solutions. We work closely with you to ensure every detail aligns with your vision while meeting all regulatory and performance requirements."
  },
  {
    number: "03",
    title: "DOCUMENTATION",
    image: "/img/p5.jpg",
    description: "We prepare comprehensive construction documentation including detailed drawings, specifications, and schedules. This stage ensures all design intent is clearly communicated to builders and trades, providing the foundation for accurate pricing and quality construction."
  },
  {
    number: "04",
    title: "APPROVALS",
    image: "/img/p3.avif",
    description: "We navigate the regulatory approval process, preparing and submitting all required documentation to local authorities. Our experience ensures efficient processing while maintaining design integrity throughout the approval journey."
  },
  {
    number: "05",
    title: "TENDER & CONTRACT",
    image: "/img/p5.jpg",
    description: "We assist in selecting the right builder through a competitive tender process. Our detailed documentation ensures accurate pricing and we provide guidance throughout contract negotiations to protect your interests."
  },
  {
    number: "06",
    title: "CONSTRUCTION",
    image: "/img/p5.jpg",
    description: "During construction, we provide ongoing support and regular site visits to ensure the build aligns with the design intent. We're your advocate throughout the construction process, addressing any issues that arise and ensuring quality outcomes."
  }
];

export default function Process() {
  const containerRef = useRef<HTMLDivElement>(null);
  const processSectionRef = useRef<HTMLDivElement>(null);

  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end end"]
  });

  // Parallax effects
  const heroParallax = useTransform(scrollYProgress, [0, 0.3], [0, -100]);
  const overviewParallax = useTransform(scrollYProgress, [0.2, 0.5], [0, -50]);

  // GSAP animations for process cards (desktop only)
  useEffect(() => {
    if (!processSectionRef.current) return;
    
    // Check if we're on mobile
    const isMobile = window.innerWidth < 768;
    if (isMobile) {
      // Clear any existing ScrollTriggers on mobile
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
      return;
    }

    const items = gsap.utils.toArray(".process_stages_item") as Element[];

    if (items.length === 0) return;

    // Clear any existing ScrollTriggers
    ScrollTrigger.getAll().forEach(trigger => trigger.kill());

    // Set initial states for all items
    items.forEach((item: Element, index: number) => {
      const container = item.querySelector(".process_stages_contain");
      const overlay = item.querySelector(".process_stages_overlay");

      gsap.set(container, {
        scale: 1,
        rotationX: 0,
        rotationY: 0,
        z: 0,
        transformOrigin: "50% 10%"
      });

      gsap.set(overlay, {
        opacity: 0
      });
    });

    // Create stacking animation for each item
    items.forEach((item: Element, index: number) => {
      const container = item.querySelector(".process_stages_contain");
      const overlay = item.querySelector(".process_stages_overlay");
      const nextItem = items[index + 1];
      
      if (index < items.length - 1) {
        // For all items except the last one, create stacking animation
        ScrollTrigger.create({
          trigger: nextItem,
          start: "top bottom",
          end: "top top",
          scrub: true,
          onUpdate: (self) => {
            const progress = self.progress;
            const scale = 1 - (progress * 0.1); // Scale down slightly
            const rotationX = progress * -15; // Rotate on X axis
            const rotationY = (index % 2 === 0 ? 1 : -1) * progress * 12; // First card rotates right, second left, etc.
            const z = progress * -200; // Move back in 3D space
            const overlayOpacity = progress * 0.3; // Add overlay

            gsap.set(container, {
              scale: scale,
              rotationX: rotationX,
              rotationY: rotationY,
              z: z,
              transformOrigin: "50% 10%"
            });

            gsap.set(overlay, {
              opacity: overlayOpacity
            });
          }
        });
      }
    });

    return () => {
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
    };
  }, []);

  return (
    <main ref={containerRef} className="bg-white py-52">
      {/* Hero Section */}
      <section className="min-h-screen relative flex items-center py-16 md:py-0">
        <motion.div
          className="w-full px-5 md:px-16"
          style={{ y: heroParallax }}
        >
          <div className="mx-auto ">
            {/* Section label on the left */}
            <div className="flex flex-col md:flex-row mb-12 md:mb-0">
              <div className="w-full md:w-1/4 mb-8 md:mb-0">
                <h3 className="text-md font-semibold uppercase tracking-widest text-black">
                  (PROCESS)
                </h3>
              </div>
              
              {/* Text content on the right */}
              <div className="w-full md:w-3/4">
                <motion.h1 
                  className="text-4xl md:text-7xl font-bold text-black leading-sung mb-8 md:mb-12"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.8 }}
                >
                  Great architecture isn't just about talent and experience, but collaborations and relationships.
                </motion.h1>
                
                {/* Image - positioned after the text */}
                <motion.div
                  className="aspect-video relative overflow-hidden"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ duration: 1, delay: 0.5 }}
                >
                  <Image
                    src="/img/p1.jpg"
                    alt="Modern architecture exterior"
                    fill
                    className="object-cover"
                  />
                </motion.div>
              </div>
            </div>
          </div>
        </motion.div>
      </section>

      {/* Our Approach Section */}
      <section className="py-20 px-5 md:px-16 bg-white">
        <div className=" mx-auto">
          <div className="flex flex-col md:flex-row">
            {/* Left side - Title */}
            <div className="w-full md:w-1/4 mb-8 md:mb-0">
              <motion.h3 
                className="text-md font-semibold uppercase tracking-widest text-black"
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.8 }}
                viewport={{ once: true }}
              >
                (OUR APPROACH)
              </motion.h3>
            </div>
            
            {/* Right side - Content */}
            <div className="w-full md:w-3/4">
              <motion.div
                className="space-y-8"
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.8, delay: 0.2 }}
                viewport={{ once: true }}
              >
                <p className="text-2xl font-medium md:text-2xl text-black leading-relaxed">
                  You can expect our team to expertly guide your project and work closely with you at every stage from delivering the initial design concepts to achieving a final build that goes beyond your aspirations
                </p>

                <motion.div
                  initial={{ opacity: 0 }}
                  whileInView={{ opacity: 1 }}
                  transition={{ duration: 0.5, delay: 0.3 }}
                  viewport={{ once: true }}
                >
                  <Link
                    href="/contact"
                    className="inline-block bg-black text-white px-8 py-3 text-md font-semibold uppercase tracking-widest hover:bg-gray-800 transition-colors duration-300"
                  >
                    Send us an enquiry
                  </Link>
                </motion.div>
                
                
              </motion.div>
            </div>
          </div>
        </div>
      </section>

      {/* Overview Section with Dark Background */}
      <section className="relative min-h-screen bg-black text-white overflow-hidden">
        {/* Background Image with parallax */}
        <motion.div 
          className="absolute inset-0"
          initial={{ scale: 1.2 }}
          whileInView={{ scale: 1 }}
          transition={{ duration: 1.5 }}
          viewport={{ once: true }}
        >
          <Image
            src="/img/p2.avif"
            alt="Architecture detail"
            fill
            className="object-cover opacity-60"
          />
         
        </motion.div>

        {/* Content */}
        <motion.div
          className="relative z-10 flex items-start min-h-screen px-5 md:px-16 pt-7"
          style={{ y: overviewParallax }}
        >
          <div className="max-w-5xl">
            <motion.div
              initial={{ opacity: 0, y: 50 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
              viewport={{ once: true }}
            >
              <h2 className="text-5xl md:text-[9rem] font-semibold text-white leading-none mb-8">
                OVERVIEW OF<br />
                OUR 6-STAGE<br />
                PROCESS
              </h2>
            </motion.div>

            
            <motion.div
              className="absolute bottom-12 right-5 md:right-12"
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              transition={{ duration: 0.5, delay: 1 }}
              viewport={{ once: true }}
            >
              <div className="flex items-center text-white text-4xl">
                <span className="mr-1 text-[7rem] leading-none">(</span>
                <svg 
                  className="w-28 h-28 text-white" 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path 
                    strokeLinecap="round" 
                    strokeLinejoin="round" 
                    strokeWidth={2} 
                    d="M19 14l-7 7m0 0l-7-7m7 7V3" 
                  />
                </svg>
                <span className="ml-1 text-[7rem] leading-none ">)</span>
              </div>
            </motion.div>
          </div>
        </motion.div>
      </section>

      {/* Process Stages with updated structure - Black Background */}
      <section ref={processSectionRef} className="bg-black">
        <div className="process_stages_collection">
          <div className="process_stages_list">
            {processStages.map((stage, index) => (
              <ProcessStage key={index} {...stage} index={index} />
            ))}
          </div>
        </div>
      </section>
      <ProcessHome showButton={false}/>
    </main>
  );
}

type ProcessStageProps = {
  number: string;
  title: string;
  description: string;
  image: string;
  index: number;
};

function ProcessStage({
  number,
  title,
  description,
  image,
  index,
}: ProcessStageProps) {
  return (
   <div
  className="process_stages_item md:sticky md:top-16"
  style={{ perspective: "250vw" }}
>
  <div
    className="process_stages_contain bg-white pt-12 px-5 md:pr-8 md:pb-28 md:pl-8 lg:pr-16 lg:pl-16"
    style={{ transformStyle: "preserve-3d", transformOrigin: "50% 10%" }}
  >
    {/* Overlay */}
    <div className="process_stages_overlay absolute inset-0 bg-black opacity-0 pointer-events-none z-10"></div>

    {/* Content */}
    <div className="process_stages_content grid grid-cols-12 gap-8 max-w-7xl mx-auto items-start">
      {/* Number on the left */}
      <div className="process_stages_index col-span-3 flex justify-start">
        <span className="text-6xl md:text-8xl font-bold text-black opacity-20 md:opacity-100">
          ({number})
        </span>
      </div>

      {/* Right Side: Title, Image, Paragraph */}
      <div className="col-span-9 flex flex-col gap-6">
        {/* Title */}
        <h2 className="process_stages_heading text-3xl md:text-6xl font-bold uppercase text-black leading-tight">
          {title}
        </h2>

        {/* Image */}
        <div className="process_stages_cover relative w-full max-w-6xl aspect-[16/9] md:aspect-[16/8] overflow-hidden">
          <div className="g_image_cover absolute inset-0 bg-black opacity-0 invisible"></div>
          <Image
            src={image}
            alt={title}
            fill
            className="process_stages_image object-cover"
          />
        </div>

        {/* Description */}
        <p className="process_stages_paragraph text-lg md:text-xl leading-relaxed text-black max-w-3xl font-semibold">
          {description}
        </p>
      </div>
    </div>
  </div>
</div>

  );
}